/* Global styles */
body {
  background-color: #a5d018;
}

.container1 {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px; /* Add margin bottom for spacing */
}

.output {
  border: 2px solid black;
  padding: 20px;
  width: 70%;
  background-color: rgb(141, 183, 247);
  border: 5px solid rgb(215, 18, 18);
}

/* Component-specific styles */
.welcome,
.user-info,
.user-type {
  padding: 10px;
  margin-bottom: 10px;
}

.user-type {
  font-weight: bold; /* Make user type text bold */
}

.student-details {
  margin-top: 20px;
  border: 2px solid black;
  padding: 20px;
  width: 70%;
  display: flex;
  flex-direction: row; /* Display cards side by side */
  flex-wrap: wrap; /* Allow cards to wrap to the next line if necessary */
  justify-content: center; /* Center content horizontally */
  background-color: rgb(20, 102, 225);
  border: 5px solid rgb(55, 179, 46);
}

.student-card {
  border: 1px solid #0f0bdf;
  border-radius: 5px;
  padding: 10px; /* Reduced padding for a smaller size */
  margin: 10px; /* Add margin between cards */
  background-color: #e411d6; /* Add background color */
  width: calc(50% - 20px); /* Set width of each card, subtracting margin */
  max-width: 300px; /* Limit max width of the cards */
  background-color: rgb(25, 226, 11); /* Change the background color */
  box-shadow: 0 4px 8px rgb(0, 0, 0);
}

.student-card h3 {
  margin-top: 0;
  font-size: 16px; /* Reduced font size of student name */
}

.student-card p {
  margin: 5px 0; /* Reduced paragraph margin */
}

.cont2 {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

/* Button styling */
button {
  background-color: rgb(255, 0, 0); /* Red background color */
  color: rgb(9, 9, 9);
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: transform 0.2s ease; /* Add animation to button */
}

/* Button hover effect */
button:hover {
  transform: translateY(-3px); /* Move button up on hover */
}

.student-card:hover{
  box-shadow: 6 6px 12px rgba(0, 0, 0, 0.1);
  transform: translateY(-30px) translateX(-30px);
}

/* Button container */
.button-container > div {
  margin-right: 20px; /* Adjust this value as needed */
}

/* Add space between buttons in the button container */
.button-container > div:not(:last-child) {
  margin-right: 20px; /* Add more space between buttons */
}

/* Set Admin button */
#admin-button {
  background-color: #4CAF50; /* Green background color */
}

/* Set Default button */
#default-button {
  background-color: #FFA500; /* Orange background color */
}
#user-button {
  background-color: #6a00ff; /* Orange background color */
}
